import Vue from 'vue';
import App from './App.vue';
import router from './router';
import { Table, TableColumn, Button } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.config.productionTip = false;
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Button);

new Vue({
  router,
  render: (h) => h(App),
}).$mount('#app');
